<template>
  <!-- 编辑商品 -->
  <div class="editor-body">
    <div class="editor-header">
      <a-breadcrumb separator=">">
        <a-breadcrumb-item>商品管理</a-breadcrumb-item>
        <a-breadcrumb-item
          ><a href="/productlist">商品列表</a></a-breadcrumb-item
        >
        <a-breadcrumb-item>{{ this.$route.meta.title }}</a-breadcrumb-item>
      </a-breadcrumb>
    </div>
    <hr style="color: #666" />
    <h3>{{ this.$route.meta.title }}</h3>
    <div class="editor-conter">
      <div><h3>商品基本信息</h3></div>
      <div>
        <span>*商品类型：</span>
        <span>
          <select
            style="outline: none; width: 200px; height: 40px"
            ref="classId"
            v-model="detlist.classId"
          >
            <option v-for="(item, index) in list" :key="index" :value="item.id">
              {{ item.className }}
            </option>
          </select>
        </span>
      </div>
      <div>
        <span>*商品名称：</span>
        <span>
          <input type="text" v-model="goodsName" />
          <br />
          商品标题名称长度至少—个字符，不能超过50个字
        </span>
      </div>
      <div>
        <span>*库存数：</span
        ><span><input type="text" v-model="goodsNum" /></span>
      </div>
      <div>
        <span>商品简介：</span
        ><span><input type="text" v-model="brief" /></span>
      </div>
      <div>
        <span>商品图片：</span>
        <Upload @onChange="onChange" :scid="pic" :limit="3" />
      </div>
      <div>
        <span> 商品详情图： </span>
        <Upload @onChange="onDetails" :scid="picid" :limit="5" />
      </div>
    </div>
    <div class="editor-buttom">
      <button>取消</button>
      <button @click="submit">上架提交</button>
    </div>
  </div>
</template>

<script>
import Upload from "../../components/upload.vue";
export default {
  components: {
    Upload,
  },
  data() {
    return {
      loading: false,
      goodsName: "",
      goodsNum: "",
      list: [],
      editor: [],
      brief: "",
      pic: [],
      picid: [],
      merGoodsPicIds: "",
      detlist: [],
    };
  },
  mounted() {
    this.datalist();
    this.classlist();
    this.byid();
  },
  methods: {
    async classlist() {
      let { data } = await this.$http.classlist();
      this.list = [...data.list];
    },
    async submit() {
      console.log(this.pic);
      console.log(this.merGoodsPicIds);
      let str = {
        classId: this.$refs.classId.value,
        goodsName: this.goodsName,
        goodsNum: this.goodsNum,
        id: this.$route.query.id,
        merId: sessionStorage.getItem("id"),
        goodsBrief: this.brief,
        mainPicId: Number(this.pic),
        merGoodsPicIds: this.merGoodsPicIds,
      };
      let { data } = await this.$http.edit(str);
      if (data.code == "0000") {
        this.$message.success("编辑商品成功可返回商品列表");
      }
    },
    async byid() {
      let str = {
        merGoodsId: this.$route.query.id,
      };
      let { data } = await this.$http.piclist(str);

      if (data.code == "0000") {
        console.log(data);
      }
    },
    async datalist() {
      let str = {
        id: this.$route.query.id,
      };
      let { data } = await this.$http.details(str);
      if (data.code == "0000") {
        this.detlist = data.details;
        console.log(this.detlist);
        this.goodsName = this.detlist.goodsName;
        this.goodsNum = this.detlist.goodsNum;
        this.brief = this.detlist.goodsBrief;
      }
    },
    async onChange(fileList) {
      let from = new FormData();
      from.append("file", fileList);
      from.append("isMain", 1);
      let { data } = await this.$http.uploadimg(from);
      console.log(data.data);
      if (data.code == "0000") {
        let str = new Array();
        str = data.data.sussList;
        str.map((item) => {
          this.pic.push(item.id);
        });
      } else {
        return;
      }
    },
    async onDetails(fileList) {
      let from = new FormData();
      from.append("file", fileList);
      from.append("isMain", 0);
      let { data } = await this.$http.uploadimg(from);
      console.log(data);
      if (data.code == "0000") {
        let str = new Array();
        str = data.data.sussList;
        str.map((item) => {
          this.picid.push(item.id);
        });
      } else {
        return;
      }
      this.merGoodsPicIds = this.picid.join(",");
    },
  },
};
</script>

<style scoped>
.editor-conter div {
  display: flex;
  justify-content: left;
  align-items: center;
  border: 1px solid rgb(146, 146, 146);
  border-bottom: 0;
  padding: 10px 300px;
}
.editor-conter input {
  width: 700px;
  height: 100px;
  border: 1px solid #c0c0c0;
  outline: none;
}
.editor-conter div span {
  width: 10%;
  text-align: right;
}
.editor-conter > :nth-child(3) > :nth-child(2) {
  width: 90%;
  text-align: left;
}
.editor-conter > :nth-child(1) {
  height: 40px;
  padding-left: 10px;
}

.editor-conter > :nth-child(4) input {
  width: 400px;
  height: 40px;
  border: 1px solid #c0c0c0;
}

.editor-conter > :nth-child(7) > :nth-child(2) {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0;
}
.editor-conter > :nth-child(6) > :nth-child(2) {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0;
}
.editor-conter > :nth-child(7) {
  border: 1px solid rgb(146, 146, 146);
}
.editor-buttom {
  text-align: center;
}
.editor-buttom button {
  width: 120px;
  height: 40px;
  margin-top: 20px;
  margin-right: 10px;
  background: #fff;
  border: 1px solid #c0c0c0;
  outline: none;
}
</style>